<template>
  <div id="wrapper">
    <div class="content">
      <div class="middle" v-if="currentPage ==1">
        <img class="title" src="./img/title.png">
        <p class="title-info">拥有终生绑定的小伙伴</p>
        <p class="title-info">与您一同探索NU SKIN的奇妙世界</p>

          <div v-on:click="olduser" class="user animated flipInX">
            <p>我身边已有好友</p>
            <p>在使用如新</p>
          </div>


          <br/>

          <img class="or" src="./img/or.png">
          <br/>
          <div v-on:click="newuser" class="user two animated flipInX">
            <p>我想成为第一个</p>
            <p>使用如新的人</p>
          </div>
      </div>

      <div class="middle2" v-else-if="currentPage ==2">
        <div class="title">
          <img class="pic" src="./img/pic01.png">
          <span class="info">输入您好友的</span>
          <img class="pic" src="./img/pic01.png"><br/>
          <span class="info">微信号/真实姓名/CN编号</span><br/>
          <span class="info">获取其购买渠道</span>
        </div>
        <div class="middle2-content">
          <span>微信号/真实姓名/CN编号</span><br/>
          <input ref="input" placeholder="请输入提示文本"/><br/>
          <button v-on:click="fetchuser">立即获取</button>
        </div>
      </div>

      <div class="middle3" v-else-if="currentPage ==3">
        <img class="userimg" v-bind:src="imgUrl">
      </div>

      <div class="middle4" v-else-if="currentPage ==4">
        <div class="title">
          <img class="pic" src="./img/pic02.png">
          <span class="info title-text">点击下方按钮</span>
          <img class="pic" src="./img/pic02.png"><br/>
          <span class="info">系统将为您匹配</span><br/>
          <span class="info">专属于您的NU SKIN之旅</span>
          <button v-on:click="fetchnewuser">开启NU SKIN之旅</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '../../api'
  // import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import lessonForm from '@/component/lessonForm/lessonForm'
  import zmFooter from '@/component/zm-footer/zm-footer'
  import editBanner from '@/component/banner/banner'
  import urlConfig from '@/config'

  export default {
    name: "home",
    data() {
      return {
        currentPage: 1,
        imgUrl: ''
      }
    },
    mounted() {
      window.addEventListener('scroll', () => {
        if (window.scrollY > 350) {
          this.showDialog = true;
        } else {
          this.showDialog = false;
        }
      }, false)
    },
    components: {
      lessonForm,
      zmFooter,
      editBanner,
      // swiper,
      // swiperSlide,
    },
    methods: {
      toggleDialogForm() {
      },
      olduser(){
        this.currentPage = 2
      },
      fetchuser(){
        console.log()
        api.fetchLogicData()
          .then(res =>{
            console.log(res)
            if(res.data.code ==1){
              var value =this.$refs.input.value
              if(!value || value ==''){
                alert("请输入提示文本")
                return
              }
              var data = res.data.data
              var keyArr = data[0]
              var imgArr = data[1]
              for(let i=0 ;i<keyArr.length; i++){
                var itemArr = keyArr[i]
                for(let j=0; j<itemArr.length ; j++){
                  if(itemArr[j] == value){
                    this.imgUrl = imgArr[i].uri
                    this.currentPage = 3
                    return
                  }
                }
              }
              alert("抱歉~搜索不到噢！")
//              keyArr.map((item,index) =>{
//                item.map((itm,idx) =>{
//                  if(value == itm){
//                    this.imgUrl = imgArr[index].uri
//                    this.currentPage = 3
//                  }
//                })
//              })
            }else {
              alert(res.data.msg || "出错拉")
            }
          })
      },
      fetchnewuser(){
        api.fetchData()
          .then(res => {
            console.log(res)
            if (res.data.code == 1) {
              var data = res.data.data
              var number = Math.floor(Math.random() * data.length)
              this.imgUrl = data[number].uri
              console.log(number)
              this.currentPage = 3
            } else {
              alert(res.data.msg || "出错了")
            }
          })
      },
      newuser(){
        this.currentPage = 4
      }
    }
  };
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/less" scoped>
  @import "../../common/css/mixin.scss";
  @import "../../common/css/animate.css";

  #wrapper {
    position: relative;
    .content {
      width: px2rem(750);
      height: px2rem(1415);
      background: url("./img/bg.png") no-repeat center;
      background-size: cover;
      text-align: center;
      display: inline-block;
      .middle {
        .title {
          margin-top: px2rem(100);
          margin-bottom: px2rem(20);
          width: px2rem(546);
          height: px2rem(102);
        }
        .title-info {
          font-size: px2rem(45);
          font-weight: bold;
          color: #fff;
        }
        .or {
          display: inline-block;
          width: px2rem(138);
          height: px2rem(68);
          margin-top: px2rem(120);
        }
        .two {
          margin-top: px2rem(150) !important;
        }
        .user {
          display: inline-block;
          width: px2rem(500);
          padding-top: px2rem(30);
          height: px2rem(150);
          background: #ef74ab;
          border-radius: px2rem(20);
          -moz-box-shadow: 0px 1px 20px #333333;
          -webkit-box-shadow: 0px 1px 20px #333333;
          box-shadow: 0px 1px 20px #333333;
          margin-top: px2rem(50);
          p {
            font-size: px2rem(40);
            color: #fff;
          }
        }
      }

      .middle2 {
        margin-top: px2rem(120);
        .title {
          .pic {
            width: px2rem(63);
            height: px2rem(31);
          }
          .info {
            display: inline-block;
            font-size: px2rem(50);
            font-weight: bold;
            color: #fff;
            margin-top: px2rem(17);
          }
        }
        .middle2-content {
          span {
            display: inline-block;
            font-size: px2rem(30);
            color: #fff;
            margin-top: px2rem(60);
            margin-right: px2rem(170);
          }
          input {
            width: px2rem(500);
            height: px2rem(90);
            background: #fff;
            margin-top: px2rem(30);
            border-radius: px2rem(10);
          }
          button {
            width: px2rem(500);
            height: px2rem(90);
            background: #f074ac;
            border: 0px;
            font-size: px2rem(25);
            color: #fff;
            margin-top: px2rem(30);
            border-radius: px2rem(20);
          }
        }
      }

      .middle3 {
        margin-top: px2rem(50);
        .userimg {
          width: px2rem(720);
          height: px2rem(1215);
        }
      }
      .middle4 {
        margin-top: px2rem(150);
        .title {
          .pic {
            width: px2rem(41);
            height: px2rem(56);
          }
          .title-text {
            margin-bottom: px2rem(50);
          }
          .info {
            display: inline-block;
            font-size: px2rem(50);
            font-weight: bold;
            color: #fff;
            margin-top: px2rem(17);
          }
          button {
            width: px2rem(500);
            height: px2rem(90);
            background: #f074ac;
            border: 0px;
            font-size: px2rem(40);
            color: #fff;
            margin-top: px2rem(30);
            border-radius: px2rem(20);
          }
        }
      }
    }
  }
</style>



